.CalculatorInsurence {
    font-family: Manrope;
    font-size: 14px;
    font-weight: 400;
    line-height: 16.8px;
    margin: 0;
    width: 16px;
    height: 16px;
    border-radius: 1px;
    border: 1.5px solid #333;
}

.CalculatorInsurence-text {
    margin: 0 0 0 10px;
}

.CalculatorInsurence-oldCost {
    font-family: Manrope;
    font-size: 10px;
    font-weight: 400;
    line-height: 12px;
    color: #878787;
    text-decoration: line-through;
    margin: 0 0 5px 5px;
}


.CalculatorInsurence-helpButton {
    width: 12px;
    height: 12px;
    background-image: url(../images/help.png);
    border: 0;
    margin: 0 0 5px 5px;
}